@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    @apply scroll-smooth;
  }

  body {
    @apply font-sans bg-zinc-50 text-zinc-900 dark:bg-zinc-950 dark:text-zinc-50 transition-colors duration-300 ease-in-out scroll-smooth;
  }

  a {
    @apply text-accent font-medium transition-colors duration-200 ease-out;
  }
}

@layer components {
  .section-title {
    @apply text-2xl font-semibold tracking-tight text-zinc-800 dark:text-zinc-100;
  }

  .badge {
    @apply inline-flex items-center gap-2 rounded-full bg-zinc-100 dark:bg-zinc-800 px-3 py-1 text-sm text-zinc-600 dark:text-zinc-300 border border-zinc-200 dark:border-zinc-700;
  }

  .card {
    @apply rounded-xl border border-zinc-200 dark:border-zinc-800 bg-white dark:bg-zinc-900/70 shadow-sm transition-all duration-300 ease-out hover:-translate-y-1 hover:shadow-lg;
  }

  .project-image {
    @apply rounded-lg border border-zinc-200 dark:border-zinc-800 bg-zinc-100 dark:bg-zinc-800;
    max-width: 100%;
    height: auto;
    max-height: 8rem;
    object-fit: contain;
  }

  .btn-primary {
    @apply inline-flex items-center justify-center rounded-full bg-accent text-white px-6 py-3 text-sm font-semibold shadow-sm transition-all duration-200 ease-out hover:-translate-y-0.5 hover:bg-accent-dark focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-accent;
  }

  .btn-secondary {
    @apply inline-flex items-center justify-center rounded-full border border-zinc-300 dark:border-zinc-700 px-6 py-3 text-sm font-semibold text-zinc-700 dark:text-zinc-200 transition-all duration-200 ease-out hover:-translate-y-0.5 hover:bg-zinc-100 dark:hover:bg-zinc-800;
  }
}

@layer utilities {
  .grid-cols-auto-fit {
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  }
}
